.hyp-row-wrap {
  width: 100%;
  position: relative;
  -webkit-transition: .2s padding ease;
  transition: .2s padding ease;
  border: 1px dashed rgba(170, 170, 170, .4);
}
.hyp-row-wrap .hyp-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}
