.hyp-bc-wrap {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 50;
  overflow: auto;
  display: none;
  background-color: rgba(0, 0, 0, .2);
}
.hyp-bc-wrap.open {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.hyp-bc {
  position: relative;
  background-color: #fff;
  width: 100%;
  max-width: 40rem;
  margin: auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-radius: .25rem;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
}
.hyp-bc-close {
  position: absolute;
  right: 0;
  top: 0;
  margin-right: .75rem;
  margin-top: .75rem;
  color: #8795a1;
}
.hyp-bc-heading {
  background-color: #f1f5f8;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
}
.hyp-bc-title {
  font-size: .875rem;
  padding: 1rem;
  color: #8795a1;
}
.hyp-bc-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.hyp-bc-single-wrap {
  width: 33.33333%;
}
.hyp-bc-single {
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
  border-radius: .25rem;
  margin: .5rem;
  padding: .5rem;
  border-width: 1px;
  border-color: transparent;
  background-color: #fff;
  cursor: pointer;
}
.hyp-bc-single .hyp-bc-single-title {
  font-size: .875rem;
  font-weight: 600;
  color: #606f7b;
  margin-bottom: .5rem;
}
.hyp-bc-single .hyp-bc-single-desc {
  font-size: .75rem;
  color: #8795a1;
}
.hyp-bc-single:hover {
  border-width: 1px;
  border-color: #3490dc;
}
