.hyp-block .hyp-block-wrapper .card {
  border-top: none;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header {
  border-top: 1px solid #eee;
  padding: 0;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header ul {
  margin: 0;
  padding: 0;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header ul li {
  margin-bottom: 0;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header ul li a {
  border: none;
  border-radius: 0;
  padding: 15px;
  font-size: 14px;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header ul:active {
  outline: none !important;
}
.hyp-block .hyp-block-wrapper .card .tabs .card-header ul:focus {
  outline: none;
}
