.hyp-nester-desc {
  font-size: .75rem;
  color: #b8c2cc;
  text-align: center;
}
.hyp-nester-btn-wrap {
  text-align: center;
  margin-bottom: .5rem;
}
.hyp-nester-add-btn {
  padding-left: .5rem;
  padding-right: .5rem;
  border-radius: .25rem;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
  color: #8795a1;
  margin-top: .25rem;
  padding-top: .2rem;
  padding-bottom: .2rem;
