.hyp-app {
  font-family: system-ui, BlinkMacSystemFont, -apple-system, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}
.hyp-topbar {
  background-color: #fff;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
  margin-left: .5rem;
  -webkit-transition: .5s ease;
  transition: .5s ease;
}
.hyp-topbar-btn {
  background-color: #fff;
  color: #3d4852;
  font-weight: 600;
  padding-top: .25rem;
  padding-bottom: .25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-right-width: 1px;
  border-color: #dae1e7;
  -webkit-transition: .2s ease;
  transition: .2s ease;
}
.hyp-topbar-btn:focus {
  outline: 0;
}
.hyp-topbar-btn:first-child {
  border-top-left-radius: .25rem;
  border-bottom-left-radius: .25rem;
}
.hyp-topbar-btn:last-child {
  border-top-right-radius: .25rem;
  border-bottom-right-radius: .25rem;
}
.hyp-topbar-btn:hover,
.hyp-topbar-btn.active {
  background-color: #3490dc;
  color: #fff;
}
.hyp-source {
  background-color: #f1f5f8;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-width: 2px;
  border-color: #f1f5f8;
  border-radius: .25rem;
  width: 100%;
  padding-top: .5rem;
  padding-bottom: .5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  color: #606f7b;
  line-height: 1.25;
  font-size: .875rem;
  height: 16rem;
}
.hyp-source:focus {
  outline: 0;
  background-color: #fff;
  border-color: #dae1e7;
}
