.hyp-edit-panel-btn {
  padding-left: .75rem;
  padding-right: .75rem;
  padding-top: .25rem;
  padding-bottom: .25rem;
  background-color: #fff;
  border-radius: .25rem;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
}
.hyp-edit-panel-btn:hover {
  background-color: #3490dc;
  color: #fff;
}
.hyp-edit-panel-btn.save {
  background-color: #38c172;
  color: #fff;
}
.hyp-edit-panel-btn.save:hover {
  background-color: #1f9d55;
}
.editPanel {
  max-width: 350px;
  min-width: 350px;
  overflow-y: scroll;
  min-height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: #f5f5f5;
  -webkit-box-shadow: 0 0 4px 0 #ddd;
          box-shadow: 0 0 4px 0 #ddd;
  padding: 15px;
  z-index: 6000;
}
.editPanel .editPanelActionBar {
  margin-bottom: 15px;
  text-align: right;
}
.editPanel .editPanelActionBar b-btn {
  -webkit-box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
          box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
  margin: 0;
  padding: 5px 8px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.editPanel .editPanelActionBar b-btn i {
  display: inline-grid;
  vertical-align: middle;
}
.editPanel .editPanelActionBar b-btn i svg {
  width: 13px;
  height: 13px;
}
.editPanel .editPanelActionBar b-btn:hover {
  background: #007bff;
  color: #fff;
  cursor: pointer;
}
.editPanel .hyp-edit-panel-wrap {
  height: 100%;
  position: relative;
}
.editPanel .hyp-edit-panel-wrap div ul {
  width: 100%;
  text-align: justify;
}
.editPanel .hyp-edit-panel-wrap div ul li {
  margin-bottom: 0;
}
.editPanel .hyp-edit-panel-wrap div ul li a {
  font-size: 14px;
  line-height: 14px;
}
.editPanel .hyp-edit-panel-wrap div ul li a.nav-link {
  border: none;
  border-radius: 0;
}
.editPanel .hyp-edit-panel-wrap div ul li a.nav-link .active {
  border: none;
  border-radius: 0;
}
.editPanel .hyp-edit-panel-wrap div ul:focus {
  outline: none;
}
.editPanel .hyp-edit-panel-wrap .tab-content fieldset .form-group .field-wrap select.form-control {
  font-size: 14px;
  padding: 4px 10px;
  height: 30px;
  border-radius: 3px;
  width: 100%;
  border: 1px solid #ebebeb;
}
.editPanel .hyp-edit-panel-wrap .vue-form-generator fieldset .form-group {
  padding: 12px;
}
.editPanel .hyp-edit-panel-wrap .vue-form-generator fieldset .form-group .field-wrap .form-control {
  font-size: 14px;
  padding: 4px 10px;
  height: 32px;
  border-radius: 3px;
  width: 100%;
  border: 1px solid #ebebeb;
  background: #f9f9f9;
  color: #848484;
}
.editPanel .hyp-edit-panel-wrap textarea.form-control {
  font-size: 14px;
  padding: 4px 10px;
  height: 32px;
  border-radius: 3px;
  width: 100%;
  border: 1px solid #ebebeb;
  background: #f9f9f9;
  color: #848484;
}
.editPanel fieldset fieldset .form-group label span {
  font-size: 13px;
  font-weight: normal;
}
.editPanel fieldset {
  border: 1px solid #ebebeb;
  margin: 5px 0;
  position: relative;
  display: block;
  background-color: #fff;
  border-radius: 4px;
  width: 100%;
  overflow: hidden;
  padding: 0;
}
.editPanel fieldset input:active {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset input:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset select:active {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset select:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.editPanel fieldset legend {
  font-size: 14px;
  line-height: 18px;
  font-weight: bold;
  color: #0061cc;
  background: #eee;
  padding: 8px 10px;
  margin: 0;
  width: 100%;
  border-bottom: 1px solid #dbdbdb;
}
.editPanel fieldset legend:hover {
  cursor: pointer;
}
.editPanel fieldset .form-group label {
  font-size: 14px;
  font-weight: bold;
  line-height: 18px;
  margin-bottom: 5px;
  display: block;
}
.editPanel fieldset .form-group .field-wrap .wrapper {
  position: relative;
}
.editPanel fieldset .form-group .field-wrap .wrapper .form-control {
  padding: 8px 10px;
  border-radius: 3px;
  width: 100%;
  border: 1px solid #ebebeb;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.editPanel fieldset .form-group .field-wrap .btn-group button {
  padding: 0 6px;
}
.editPanel fieldset .form-group .field-wrap .imageChooser button:active {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.editPanel fieldset .form-group .field-wrap .imageChooser button:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.editPanel fieldset .form-group+.form-group {
  padding-top: 0 !important;
}
.editPanel fieldset.willcollapse div.form-group {
  display: none;
  -webkit-transition: all .3s ease-in;
  transition: all .3s ease-in;
}
.editPanel fieldset.willcollapse.active div.form-group {
  display: block;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  padding: 12px;
}
.editPanel .ql-toolbar.ql-snow+.ql-container.ql-snow {
  position: relative;
  display: block;
  overflow: hidden;
  width: 100%;
}
.editPanel .ql-toolbar.ql-snow+.ql-container.ql-snow .ql-editor {
  max-height: 200px;
  overflow-y: scroll;
}
.editPanel .ql-snow .ql-tooltip {
  border: none;
  background: #ebebeb;
  -webkit-transform: none;
          transform: none;
  position: relative;
  left: 0 !important;
  right: 0 !important;
  margin-top: 0 !important;
  top: 0 !important;
}
