.hyper-multiObjWrapper .singleObjectWrapper {
  border: 1px solid #ebebeb;
  margin-top: 5px;
  margin-bottom: 5px;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions {
  background: #f5f5f5;
  padding: 4px;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions b-button {
  -webkit-box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
          box-shadow: rgba(0, 0, 0, .12) 0 1px 4px -1px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
  margin: 0;
  padding: 5px 8px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions b-button i {
  display: inline-grid;
  vertical-align: middle;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions b-button i svg {
  width: 13px;
  height: 13px;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions b-button:hover {
  cursor: pointer;
  background: #007bff;
  border-color: #007bff;
}
.hyper-multiObjWrapper .singleObjectWrapper .actions b-button:hover svg {
  color: #fff;
}
.hyper-multiObjWrapper .singleObjectWrapper fieldset {
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}
.hyper-multiObjWrapper .singleObjectWrapper fieldset .field-wrap {
  position: relative;
  display: block;
}
.hyper-multiObjWrapper .addItemBtn {
  line-height: 0;
}
.hyper-multiObjWrapper .addItemBtn i {
  display: inline-grid;
  vertical-align: middle;
}
.hyper-multiObjWrapper .addItemBtn i svg {
  width: 13px;
  height: 13px;
}
.hyper-multiObjWrapper .addItemBtn i.hyper-btn {
  padding: 4px 6px !important;
}
.hyper-multiObjWrapper .addItemBtn:active {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  outline: none !important;
}
.hyper-multiObjWrapper .addItemBtn:focus {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  outline: none !important;
}
.hyper-multiObjWrapper .collapsed {
  display: none;
}
