.hyp-tab-wrap {
  margin-top: .5rem;
  margin-bottom: .5rem;
}
.hyp-tab-wrap ul.hyp-tab {
  list-style: none;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.hyp-tab-wrap ul.hyp-tab li.hyp-tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 0%;
          flex: 1 1 0%;
  text-align: center;
}
.hyp-tab-wrap ul.hyp-tab li.hyp-tab-item a.hyp-tab-item-link {
  display: block;
  padding-top: .5rem;
  padding-bottom: .5rem;
  text-decoration: none;
  border-bottom-width: 1px;
  color: #8795a1;
}
.hyp-tab-wrap ul.hyp-tab li.hyp-tab-item a.hyp-tab-item-link:hover {
  border-color: #3490dc;
}
.hyp-tab-wrap ul.hyp-tab li.hyp-tab-item.is-active a.hyp-tab-item-link {
  border-color: #3490dc;
  background-color: #fff;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
}
