.hyp-actionbar {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
}
.hyp-actionbar-icon-panel {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: left;
  width: 8rem;
  font-size: .75rem;
  background-color: #dae1e7;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .1);
  padding-left: .5rem;
  padding-right: .5rem;
  display: none;
}
.hyp-actionbar-icon-panel button {
  text-align: center;
  padding-top: .25rem;
  padding-bottom: .25rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 0%;
          flex: 1 1 0%;
}
.hyp-actionbar-icon-panel button:hover {
  background-color: #fff;
}
.showControl {
  border-width: 1px;
  border-color: #3490dc;
}
.showControl .hyp-actionbar {
  display: block;
}
